import * as emp from "../../emp"

var signals = {
  methods: {

    SIGNAL_CLEAR(sender, receiver = 'cli', slot = 'slotClearTerm') {
      return {
        event: "clearTerm",
        sender: sender,
        receiver: receiver,
        slot: slot,
        kwargs: {}
      }
    },

    //  , 
    SIGNAL_DEPENDS_ON_MEMORY(sender, filename = null, receiver = 'cli', slot = "slotSendCommands") {
      return {
        event: "dependsOnMemory",
        sender: sender,
        receiver: receiver,
        slot: slot,
        kwargs: {
          filename: filename,
          command: emp.memoryAnalysing(filename)
        }
      }
    },


    // ,,
    SIGNAL_GET_CODE(sender, filename = null, receiver = 'cli', slot = "slotSendCommands") {
      return {
        event: "getCode",
        sender: sender,
        receiver: receiver,
        slot: slot,
        kwargs: {
          filename: filename,
          command: emp.getCode(filename)
        }
      }
    },

    // , ,
    SIGNAL_GET_FILE(sender, filename = null, receiver = "cli", slot = "slotGetFile") {
      return {
        event: "getFile",
        sender: sender,
        receiver: receiver,
        slot: slot,
        kwargs: {
          filename: filename,
        }
      }
    },


    SIGNAL_SEND_COMMAND(sender, command, receiver = "cli", slot = "slotSendCommands") {
      return {
        event: "sendComands",
        sender: sender,
        receiver: receiver,
        slot: slot,
        kwargs: {
          command: command,
        }
      }
    }

  }
}

export default signals
