var slots = {
  methods: {
    //,
    slotConnected() {
      this.connected = true;
    },

    slotDisconnected() {
      this.connected = false;
    },

    slotShowMemoryStatus(kwargs){
      this.memoryStatus = kwargs.data;
    },

    slotShowSysInfo(kwargs){
      this.sysInfo = kwargs.data;
    }

  }
}

export default slots
